<template>
  <div class="container">
     <div class="row">
      <div class="col-md-8">
        <div class="row mb-2" v-for="p in posts" :key="p.transaction">
          <post :post=p :show-content="false"></post>
        </div>
        <div class="row">
          <div class="col-md-2" v-if="currentPage>1">
            <router-link :to="'/e/' + sub + '?page=' + (currentPage-1)">&larr; prev</router-link>
          </div>
          <div class="col-md-2" v-if="currentPage<pages">
            <router-link :to="'/e/' + sub + '?page=' + (currentPage+1)">next &rarr;</router-link>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="row">
          <div class="col-md-12">
            <div class="row">
                <div class="col-md-6">
                    <img src="https://cdn.novusphere.io/static/atmos.svg" style="width: 100%">
                </div>
                <div class="col-md-6">
                    <img src="https://cdn.novusphere.io/static/eos.svg" style="width: 100%">
                </div>
            </div>
          </div>
          <div class="col-md-12">
             <div class="row mt-3">
                <div class="col-md-4"></div>
                <div class="col-md-4">
                  <a class="btn btn-success" href="https://github.com/Novusphere/novusphere-eos">GitHub (Source)</a>
                </div>
                <div class="col-md-4"></div>
             </div>
             <div class="row mt-3">
                <div class="col-md-4"></div>
                <div class="col-md-4">
                  <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#submitPost">Submit new post</button>
                </div>
                <div class="col-md-4"></div>
             </div>
             <div class="row mt-3 ml-3">
               <p class="text-center">
                 This is an experimental reddit-style forum built on EOS by the Novusphere community,
                 using the <a href="https://github.com/eoscanada/eosio.forum">eosio.forum</a> contract deployed by EOSCanada.
                 You can follow development on our <a href="https://discord.gg/PtXzUVr">Discord</a>.
                 You need the <a href="https://get-scatter.com/">Scatter</a> browser plugin to post.
               </p>
             </div>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="submitPost">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">New Submission</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group row">
                <label for="inputTitle" class="col-sm-2 col-form-label">Title</label>
                <div class="col-sm-10">
                  <input type="email" class="form-control" id="inputTitle" placeholder="Title" v-model="post.title">
                </div>
              </div>
              <div class="form-group row">
                <label for="inputContent" class="col-sm-2 col-form-label">Content</label>
                <div class="col-sm-10">
                  <textarea rows="10" class="form-control" id="inputContent" placeholder="Content" v-model="post.content"></textarea>
                </div>
              </div>
              <fieldset class="form-group">
                <div class="row">
                  <legend class="col-form-label col-sm-2 pt-0"></legend>
                  <div class="col-sm-10">
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentType" value="" checked v-model="post.attachment.type">
                      <label class="form-check-label">No attachment</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentType" value="url" v-model="post.attachment.type">
                      <label class="form-check-label">URL</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentType" value="ipfs" v-model="post.attachment.type">
                      <label class="form-check-label">IPFS</label>
                    </div>
                  </div>
                </div>
              </fieldset>
              <fieldset class="form-group" v-if="post.attachment.type != ''">
                <div class="row">
                  <legend class="col-form-label col-sm-2 pt-0"></legend>
                  <div class="col-sm-10">
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentDisplay" value="link" checked v-model="post.attachment.display">
                      <label class="form-check-label">link</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentDisplay" value="iframe" v-model="post.attachment.display">
                      <label class="form-check-label">iframe</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentDisplay" value="img" v-model="post.attachment.display">
                      <label class="form-check-label">image</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentDisplay" value="mp4" v-model="post.attachment.display">
                      <label class="form-check-label">mp4</label>
                    </div>
                    <div class="form-check form-check-inline">
                      <input class="form-check-input" type="radio" name="attachmentDisplay" value="mp3" v-model="post.attachment.display">
                      <label class="form-check-label">mp3</label>
                    </div>
                  </div>
                </div>
              </fieldset>
              <div class="form-group row" v-if="post.attachment.type != ''">
                <label for="inputAttachment" class="col-sm-2 col-form-label"></label>
                <div class="col-sm-10">
                  <input class="form-control" id="inputAttachment" placeholder="IPFS hash / URL" v-model="post.attachment.value">
                </div>
              </div>
            </form>
            <div class="row">
              <div class="col-md-12">
                <div class="text-center">
                  <span style="font-weight: bold">{{post.status}}</span>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" v-on:click="postContent()">Post</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { GetNovusphere } from "../novusphere"
import { GetEOS, ScatterConfig, ScatterEosOptions } from "../eos"
import { v4 as uuidv4 } from "uuid"
import jQuery from "jquery"

import Post from './Post.vue'

const MAX_ITEMS_PER_PAGE = 25;

export default {
  name: "Index",
  components: {
    'post': Post
  },
  watch: {
    '$route.query.page': function (id) {
      this.load();
    }
  },
  mounted: function() {
    this.load();
  },
  methods: {
   load: async function() {
      var currentPage = parseInt(((this.$route.query.page) ? (this.$route.query.page) : 1));
      console.log(currentPage);
      var sub = ((this.$route.params.sub) ? (this.$route.params.sub) : 'novusphere');
      var novusphere = GetNovusphere();
      var apiResult;

      apiResult = await novusphere.api({
          'count': novusphere.config.collection,
          'maxTimeMS': 1000,
          'query': { // only find top level posts
            'data.json_metadata.sub': sub, 
            'data.reply_to_post_uuid': '' 
          }
      });

      var numPages = Math.ceil(apiResult.n / MAX_ITEMS_PER_PAGE);

      apiResult = await novusphere.api({
          'aggregate': novusphere.config.collection,
          'maxTimeMS': 1000,
          'cursor': {},
          'pipeline': [
            { 
                "$match": { 
                  'data.json_metadata.sub': sub, 
                  'data.reply_to_post_uuid': '' 
                } 
            },
            { 
                "$lookup": {
                    "from": "novusphere",
                    "localField": "data.post_uuid",
                    "foreignField": "data.reply_to_post_uuid",
                    "as": "replies"
                }
            },
            { 
                "$project": {
                    "transaction": "$transaction",
                    "createdAt": "$createdAt",
                    "data": "$data",
                    "total_replies": { "$size": "$replies" }
                } 
            },
            {
                "$sort": {
                  "createdAt": -1
                }
            },
            {
                "$skip": (currentPage-1) * MAX_ITEMS_PER_PAGE
            },
            {
                "$limit": MAX_ITEMS_PER_PAGE
            }]
      });

      var payload = apiResult.cursor.firstBatch;
      //console.log(payload[0].data.title);

      this.$data.posts = payload;
      this.$data.pages = numPages;
      this.$data.currentPage = currentPage;
      this.$data.sub = sub;
  },
  postContent: async function() {
      var post = this.$data.post;

      var identity;
      this.$data.post.status = 'Getting Scatter identity...';

      try {
        identity = await window.scatter.getIdentity({
          accounts: [
            {
              chainId: ScatterConfig.chainId,
              blockchain: ScatterConfig.blockchain
            }
          ]
        });
      }
      catch (ex) {
        this.$data.post.status += ' Failed!';
        return;
      }

     var txid;
     this.$data.post.status = 'Creating tx and broadcasting to EOS...';
     try {
        const eosAccount = identity.accounts[0].name;
        const eosAuth = identity.accounts[0].authority;
        
        // make scatter eos instance
        const eos = GetEOS(window.scatter);

        var eosPost = {
                title: post.title,
                account: eosAccount,
                certify: 0,
                content: post.content,
                post_uuid: uuidv4(),
                json_metadata: JSON.stringify({
                  sub: 'novusphere',
                  attachment: {
                    value: post.attachment.value,
                    type: post.attachment.type,
                    display: post.attachment.display
                  }
                }),
                reply_to_account: "",
                reply_to_post_uuid: ""
        };

        var eosforum = await eos.contract("eosforumtest");
        var eostx = await eosforum.transaction(tx => {
            tx.post(eosPost,
              {
                authorization: [
                  {
                    actor: eosAccount,
                    permission: eosAuth
                  }
                ]
              }
            );
          });

          txid = eostx.transaction_id;
    }
    catch (ex) {
      this.$data.post.status += ' Failed!';
      return;
    }

     this.$data.post.status = 'Waiting for Novusphere to index...';

     var novusphere = GetNovusphere();
     await novusphere.waitTx(txid, 500, 1000);

     jQuery('#submitPost').modal('hide');
     this.$data.post.status = '';
     this.$router.push('/e/' + this.sub + '/' + txid);
   }
  },
  data() {
    return {
      currentPage: 0,
      pages: 0,
      sub: '',
      posts: [], // for posts being displayed
      post: { // for making a new post
        status: '',
        title: '',
        content: '',
        attachment: {
          value: '',
          type: '',
          display: 'link'
        }
      }
    };
  }
};
</script>


